<template>
  <div style="margin-top: 50px">
    <el-form :model="value" :rules="rules" label-width="120px" style="width: 600px" size="small">
      <el-form-item label="原密码：" prop="oldPassword">
        <el-input type="password" v-model="value.oldPassword"></el-input>
      </el-form-item>
      <el-form-item label="新密码：" prop="newPassword">
        <el-input type="password" v-model="value.newPassword" ></el-input>
      </el-form-item>
      <el-form-item label="确认密码：" prop="confirmPassword">
        <el-input type="password" v-model="value.confirmPassword"></el-input>
      </el-form-item>
      <el-form-item style="text-align: center">
        <el-button type="primary" size="medium" @click="commit()">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>

  import {passwordModify} from "../../api/password";
  import {getCookie} from "../../api/password";
  export default {
    data() {
      const validatePass = (rule, value, callback) => {
        if (value.length < 6||value.length>15) {
          callback(new Error('密码不能小于6位,大于15位'))
        } else {
          callback()
        }
      };
      return {
        value:{
          userName: getCookie("username"),
          oldPassword:'',
          newPassword:'',
          confirmPassword:''
        },
        rules: {
          newPassword: [{required: true, trigger: 'blur', validator: validatePass}],

        },
        //选中商品分类的值
      };
    },
    methods: {
      //处理编辑逻辑
      commit(){
        passwordModify(this.value).then(response=>{
          this.$message({
            type: 'success',
            message: '提交成功',
            duration:1000,

          });
          this.$store.dispatch('LogOut').then(() => {
            location.reload()
          })
        })
        // this.$store.dispatch('LogOut').then(() => {
        //   location.reload()
        // })
      }
    }
  }
</script>
<style scoped>
</style>



